import styled from "styled-components";

const Projects = [
  {
    name: "ChoRewards - React Native",
    link: "https://github.com/chantaldesiree",
  },
  {
    name: "Disney+ Clone",
    link: "https://github.com/chantaldesiree/disney-plus-clone",
  },
  {
    name: "Turn the Page - Ecommerce Store",
    link: "https://github.com/chantaldesiree",
  },
  {
    name: "Pokemon Database - PokeApi",
    link: "https://github.com/chantaldesiree",
  },
  {
    name: "ChoRewards - Java (Android)",
    link: "https://github.com/chantaldesiree/chore-rewards-app",
  },
  { name: "Developer Portfolio", link: "https://github.com/chantaldesiree" },
];

const Project = () => {
  return (
    <>
      {Projects.map((p) => {
        return (
          <Pro>
            {p.name}
            <Icons>
              <LinkColour>
                <i class="fas fa-link"></i>
              </LinkColour>
              <LinkColour href={p.link}>
                <i class="fab fa-github fa-lg"></i>
              </LinkColour>
            </Icons>
          </Pro>
        );
      })}
    </>
  );
};

const Pro = styled.div`
  padding: 10px;
  background-color: #36175e;
  height: 50px;
  color: #9768d1;
  min-width: 350px;
  min-height: 300px;
  margin: 10px 0;
  text-transform: uppercase;
  letter-spacing: 2px;
`;

const Icons = styled.div`
  display: flex;
  flex-direction: row;
  position: relative;
  bottom: -250px;
  left: 300px;
`;

const LinkColour = styled.a`
  display: block;
  color: #9768d1;
  padding: 3px;
`;

export default Project;
