import styled, { keyframes } from "styled-components";
import "../index.css";

import Nav from "../components/Nav";
import Project from "../components/Project";
import Desktop from "../assets/desktop.png";
import RRC from "../assets/RRC.png";

const Main = () => {
  return (
    <>
      <Container>
        <NavBarContainer>
          <Nav />
        </NavBarContainer>
        <IntroContainer>
          <Intro>
            Hi, my name is{" "}
            <span style={{ color: "#9768D1" }}>Chantal Wiebe</span>, and I'm a
            Software Developer from Winnipeg, Manitoba, Canada.
          </Intro>
          <CodingPhoto />
        </IntroContainer>
        <AboutMe>
          I'm a recent graduate of the{" "}
          <span style={{ color: "#9768D1" }}>
            Business Information Technology
          </span>{" "}
          program at Red River College.
          <AboutMeCaption>
            Where I majored in Web and Full-stack Development.
          </AboutMeCaption>
          <RRCPhoto />
        </AboutMe>
        <Projects>
          Here are some of the recent projects that I've worked on:
        </Projects>
        <ProjectContainer>
          <Project />
        </ProjectContainer>
      </Container>
    </>
  );
};

const Container = styled.div`
  display: flex;
  flex-direction: column;
  padding: 2em 8em;
  margin: auto;
  color: #7b52ab;
  overflow: hidden;
  max-width: 1150px;
`;

const NavBarContainer = styled.div`
  width: 1150px;
  position: fixed;
`;

const slideLeft = keyframes`
  from {
    margin-left: -100%;
    width: 300%;
  }

  to {
    margin-left: 0%;
    width: 300%;
  }
}
`;

const slideRight = keyframes`
  from {
    margin-right: -100%;
    width: 300%;
  }

  to {
    margin-right: 0%;
    width: 100%;
  }
}
`;

const slideUp = keyframes`
  from {
    margin-top: 100%;
    width: 300%;
  }

  to {
    margin-top: 0%;
    width: 100%;
  }
}
`;

const fadeIn = keyframes`
from {
  opacity: 0;
}

to {
  opacity: 0.05;
}`;

const IntroContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
`;

const Intro = styled.div`
  color: #553285;
  font-family: "EB Garamond", serif;
  font-size: 4.5em;
  max-width: 750px;
  padding: 2em 0;
  animation-name: ${slideLeft};
  animation-duration: 4s;
  animation-iteration-count: 1;
  flex: 4;
  z-index: 1;
`;

const CodingPhoto = styled.div`
  background-color: #25064d;
  background-image: url(${Desktop});
  background-blend-mode: screen;
  flex: 1;
  height: 465px;
  width: 700px;
  z-index: -100;
  position: absolute;
  top: 120px;
  margin-left: 500px;
  overflow: hidden;
  animation-name: ${fadeIn};
  animation-duration: 6s;
  animation-iteration-count: 1;
  opacity: 0.05;
`;

const RRCPhoto = styled.div`
  background-color: #25064d;
  background-image: url(${RRC});
  background-blend-mode: screen;
  flex: 1;
  height: 465px;
  width: 700px;
  z-index: -100;
  position: absolute;
  top: 600px;
  left: 150px;
  overflow: hidden;
  animation-name: ${fadeIn};
  animation-duration: 6s;
  animation-iteration-count: 1;
  opacity: 0.05;
`;

const AboutMe = styled.div`
  margin-left: auto;
  color: #553285;
  font-family: "EB Garamond", serif;
  font-size: 3em;
  max-width: 700px;
  padding: 0.5em 0;
  animation-name: ${slideRight};
  animation-duration: 4s;
  animation-iteration-count: 1;
`;

const AboutMeCaption = styled.div`
  color: #9768d1;
  font-family: "Comfortaa", cursive;
  font-size: 0.3em;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding-top: 1.8em;
`;

const Projects = styled.div`
  color: #553285;
  font-family: "EB Garamond", serif;
  font-size: 3em;
  max-width: 700px;
  padding: 0.5em 0;
  padding: 3em 0 0.5em 0;
  animation-name: ${slideUp};
  animation-duration: 4s;
  animation-iteration-count: 1;
`;

const ProjectContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
`;

export default Main;
