import styled from "styled-components";

import "../index.css";

const Nav = () => {
  return (
    <>
      <NavBar>
        <Name>CHANTAL WIEBE</Name>
        <SocialMedia>
          <IconWrapper>
            <LinkColour href="https://www.linkedin.com/in/chantal-wiebe/">
              <i class="fab fa-linkedin-in fa-lg"></i>
            </LinkColour>
          </IconWrapper>
          <IconWrapper>
            <LinkColour href="https://twitter.com/chantaldesireex">
              <i class="fab fa-twitter fa-lg"></i>
            </LinkColour>
          </IconWrapper>
          <IconWrapper>
            <LinkColour href="https://github.com/chantaldesiree">
              <i class="fab fa-github fa-lg"></i>
            </LinkColour>
          </IconWrapper>
        </SocialMedia>
      </NavBar>
    </>
  );
};

const NavBar = styled.div`
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 50px;
`;

const Name = styled.div`
  color: #9768d1;
  font-family: "Comfortaa", cursive;
  font-size: 1.1em;
  letter-spacing: 2px;
`;

const SocialMedia = styled.div``;

const LinkColour = styled.a`
  color: #9768d1;
`;

const IconWrapper = styled.span`
  padding: 0 0.5em;
`;

export default Nav;
